export default {
	data() {
		return {
			totalData: {},
			nowData: null,
			userCount: {},
			salesData: [],
		};
	},
	onShow() {
		let mid = this.getMerchantInfo().id;
		if(!mid){
			uni.showToast({
				title:"您还未认证商家",
				icon:"none"
			})
			setTimeout(()=>{
				uni.switchTab({
					url:"../tabbar/data"
				})
			},1000)
		}else{
			this.nowData = new Date()
			//获取总订单数/总销售额
			this.getTotalCountAndPrice();
			//获取本月新增用户
			this.getUserCount();
			//获取今日/昨日销售数据
			this.getTodayAndYesterdayData();
		}
		
	},
	methods: {
		getTotalCountAndPrice() {
			uni.request({
				url: this.prefix + "/getTotalCountAndPrice",
				data: {
					mid: this.getMerchantInfo().id
				},
				success: (res) => {
					this.totalData = res.data.data
				}
			})
		},
		getTodayAndYesterdayData() {
			uni.request({
				url: this.prefix + "/getTodayAndYesterdayData",
				data: {
					mid: this.getMerchantInfo().id
				},
				success: (res) => {
					this.salesData = res.data.data
				}
			})
		},
		getUserCount() {
			uni.request({
				url: this.prefix + "/getUserCount",
				data: {
					mid: this.getMerchantInfo().id
				},
				success: (res) => {
					this.userCount = res.data.data
				}
			})
		}
	}
}
